// 封装的是 往浏览器中存 数据 取数据  删除数据的方法


const infoKey = 'userinfo'

// 往仓库中村数据
export const setInfo = (obj) => {
    localStorage.setItem(infoKey, JSON.stringify(obj))
}

// 取本地仓库中的数据
export const getInfo = () => {
    let res = localStorage.getItem(infoKey)
    return res ? JSON.parse(res) : {
        token: '',
        userId: ''
    }
}

export const clearUserinfo=()=>{
    localStorage.removeItem(infoKey)
}

// 删除本地存储中的数据
export const removeInfo =()=>{
    localStorage.removeItem(infoKey)
}


// 存 取 用户搜索历史
const userHistoryList = 'history_list'

// 存数据的方法

export const setHistorylist=(value)=>{
    localStorage.setItem(userHistoryList,value?JSON.stringify(value):[])
}


// 取搜索历史

export const getHistoryList=()=>{
   return JSON.parse(localStorage.getItem(userHistoryList))
}

export const clearsearchList=()=>{
    localStorage.removeItem(userHistoryList)
}