<template>
  <div>
    <div class="cart" v-if="goodsTotal > 0 && isLogin">
      <van-nav-bar title="购物车" fixed />
      <!-- 购物车开头 -->
      <div class="cart-title">
        <span class="all"
          >共<i>{{ goodsTotal }}</i
          >件商品</span
        >
        <span class="edit" @click="isEdit = !isEdit">
          <van-icon name="edit" />
          编辑
        </span>
      </div>

      <!-- 购物车列表 -->
      <div class="cart-list">
        <div class="cart-item" v-for="item in cartlist" :key="item.goods_id">
          <van-checkbox
            :value="item.isChecked"
            @click="changeChecked(item.goods_id)"
          ></van-checkbox>
          <div class="show">
            <img :src="item.goods.goods_image" alt="" />
          </div>
          <div class="info">
            <span class="tit text-ellipsis-2">{{ item.goods.goods_name }}</span>
            <span class="bottom">
              <div class="price">
                ¥ <span>{{ item.goods.goods_price_min }}</span>
              </div>
              <!-- <div class="count-box">
              <button class="minus">-</button>
              <input class="inp" :value="4" type="text" readonly>
              <button class="add">+</button>
            </div> -->

              <CountBox
                :goodsNum="item.goods_num"
                @sunNum="
                  (value) => changeNum(value, item.goods_id, item.goods_sku_id)
                "
                @addnum="
                  (value) => changeNum(value, item.goods_id, item.goods_sku_id)
                "
              ></CountBox>
            </span>
          </div>
        </div>
      </div>

      <div class="footer-fixed">
        <div class="all-check">
          <van-checkbox
            icon-size="18"
            :value="isAllchecked"
            @click="toggleAll(!isAllchecked)"
          ></van-checkbox>
          全选
        </div>

        <div class="all-total">
          <div class="price">
            <span>合计：</span>
            <span
              >¥ <i class="totalPrice">{{ selPrice }}</i></span
            >
          </div>
          <div v-if="!isEdit" class="goPay" @click="goPay">结算({{ selCount }})</div>
          <div
            v-else
            class="delete"
            :class="{ disabled: selCount === 0 }"
            @click="delcartgoods"
          >
            删除
          </div>
        </div>
      </div>
    </div>
    <div class="empty-cart" v-else>
      <img src="@/assets/empty.png" alt="" />
      <div class="tips">您的购物车是空的, 快去逛逛吧</div>
      <div class="btn" @click="$router.push('/')">去逛逛</div>
    </div>
  </div>
</template>

<script>
import {getInfo} from "@/utils/storage.js"
import CountBox from "@/components/CountBox.vue";
import { mapActions, mapState, mapGetters, mapMutations } from "vuex";
export default {
  name: "CartPage",
  data() {
    return {
      // 用于控制是 结算 还是上删除
      //  true 删除   false 结算
      isEdit: false,
    };
  },
  methods: {
    ...mapActions("cart", ["getCartAction", "updatecarts", "delcarts"]),
    ...mapMutations("cart", ["toggleCheck", "toggleAllCheck"]),

    // 改变选择状态
    changeChecked(id) {
      // 通知vuex  找到对应的商品 选择状态取反

      this.toggleCheck(id);
    },

    toggleAll(value) {
      this.toggleAllCheck(value);
    },

    changeNum(value, goods_id, goods_sku_id) {
      this.updatecarts({ value, goods_id, goods_sku_id });
      console.log(value);
      // 通知 后端修改数据
    },

    // 删除购物车列表中的数据
    delcartgoods() {
      if (this.selCount < 1) return;
      this.delcarts();
      this.isEdit = false;
    },

    // goPay 去订单结算
    goPay(){
      this.$router.push({
        path:'/pay',
        query:{
          mode:'cart',

          // 获取选中的订单 拿到每一个订单的id 将id组合成一个字符串
          cartIds:this.selgooods.map(item=>item.id).join(',')
        }
      })
    }
  },

  computed: {
    ...mapState("cart", ["cartlist"]),
    ...mapGetters("cart", [
      "selCount",
      "goodsTotal",
      "selPrice",
      "isAllchecked",
      "selgooods"
    ]),
    isLogin(){
      let userinfo =getInfo()||{}
      let token=userinfo.token

      return token
    }


  },
  components: {
    CountBox: CountBox,
  },
  created() {
    this.getCartAction();
  },

  watch: {
    isEdit(newvalule) {
      //  true 删除   false 结算
      if (newvalule) {
        // 让所有的 checkbox 都是false

        this.toggleAllCheck(false);
      } else {
        // 让所有的 checkbox true
        this.toggleAllCheck(true);
      }
    },
  },
};
</script>

<style lang="less" scoped>
// 主题 padding
.cart {
  padding-top: 46px;
  padding-bottom: 100px;
  background-color: #f5f5f5;
  min-height: 100vh;
  .cart-title {
    height: 40px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 10px;
    font-size: 14px;
    .all {
      i {
        font-style: normal;
        margin: 0 2px;
        color: #fa2209;
        font-size: 16px;
      }
    }
    .edit {
      .van-icon {
        font-size: 18px;
      }
    }
  }

  .cart-item {
    margin: 0 10px 10px 10px;
    padding: 10px;
    display: flex;
    justify-content: space-between;
    background-color: #ffffff;
    border-radius: 5px;

    .show img {
      width: 100px;
      height: 100px;
    }
    .info {
      width: 210px;
      padding: 10px 5px;
      font-size: 14px;
      display: flex;
      flex-direction: column;
      justify-content: space-between;

      .bottom {
        display: flex;
        justify-content: space-between;
        .price {
          display: flex;
          align-items: flex-end;
          color: #fa2209;
          font-size: 12px;
          span {
            font-size: 16px;
          }
        }
        .count-box {
          display: flex;
          width: 110px;
          .add,
          .minus {
            width: 30px;
            height: 30px;
            outline: none;
            border: none;
          }
          .inp {
            width: 40px;
            height: 30px;
            outline: none;
            border: none;
            background-color: #efefef;
            text-align: center;
            margin: 0 5px;
          }
        }
      }
    }
  }
}

.footer-fixed {
  position: fixed;
  left: 0;
  bottom: 50px;
  height: 50px;
  width: 100%;
  border-bottom: 1px solid #ccc;
  background-color: #fff;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 10px;

  .all-check {
    display: flex;
    align-items: center;
    .van-checkbox {
      margin-right: 5px;
    }
  }

  .all-total {
    display: flex;
    line-height: 36px;
    .price {
      font-size: 14px;
      margin-right: 10px;
      .totalPrice {
        color: #fa2209;
        font-size: 18px;
        font-style: normal;
      }
    }

    .goPay,
    .delete {
      min-width: 100px;
      height: 36px;
      line-height: 36px;
      text-align: center;
      background-color: #fa2f21;
      color: #fff;
      border-radius: 18px;
      &.disabled {
        background-color: #ff9779;
      }
    }
  }
}

.empty-cart {
  padding: 80px 30px;
  img {
    width: 140px;
    height: 92px;
    display: block;
    margin: 0 auto;
  }
  .tips {
    text-align: center;
    color: #666;
    margin: 30px;
  }
  .btn {
    width: 110px;
    height: 32px;
    line-height: 32px;
    text-align: center;
    background-color: #fa2c20;
    border-radius: 16px;
    color: #fff;
    display: block;
    margin: 0 auto;
  }
}
</style>
