<template>
  <div class="search">
    <van-nav-bar fixed title="商品列表" left-arrow @click-left="$router.go(-1)" />

    <van-search
      readonly
      shape="round"
      background="#ffffff"
      :value="search"
      show-action
      @click="$router.push('/search')"
    >
      <template #action>
        <van-icon class="tool" name="apps-o" />
      </template>
    </van-search>

    <!-- 排序选项按钮 -->
    <div class="sort-btns">
      <div class="sort-item">综合</div>
      <div class="sort-item">销量</div>
      <div class="sort-item">价格 </div>
    </div>

    <div class="goods-list">
      <GoodsItem v-for="item in goodsList" :key="item.goods_id" :item="item"></GoodsItem>
    </div>
  </div>
</template>

<script>
import {getGoodsLis} from '@/api/shop.js'
import GoodsItem from '@/components/GoodsItem.vue'
export default {
  name: 'SearchIndex',
 data(){
  return{
    page:1,

    goodsList:[]
  }
 },
  components: {
    GoodsItem
  },

  computed:{
    // 用户搜索的内容 从商品搜索页传过来的
    search(){
      return this.$route.query.search
    },

    categoryId(){
     return this.$route.query.categoryId
    }

  },

 async created(){
  //                         分类id  商品名称，页数
  const a= await getGoodsLis(this.categoryId||'',this.search||'',this.page)

  this.goodsList=a.data.list.data

  console.log(this.goodsList);
  }

}
</script>

<style lang="less" scoped>
.search {
  padding-top: 46px;
  ::v-deep .van-icon-arrow-left {
    color: #333;
  }
  .tool {
    font-size: 24px;
    height: 40px;
    line-height: 40px;
  }

  .sort-btns {
    display: flex;
    height: 36px;
    line-height: 36px;
    .sort-item {
      text-align: center;
      flex: 1;
      font-size: 16px;
    }
  }
}

// 商品样式
.goods-list {
  background-color: #f6f6f6;
}
</style>