// 引入vue 和vuerouter
import VueRouter from 'vue-router' 
import Vue from 'vue'
import {getInfo} from '@/utils/storage.js'

import layout from '@/views/layout/index.vue'
import login from '@/views/login/index.vue'

const search=()=>import('@/views/search/index.vue')
// import search from '@/views/search/index.vue'
import searchList from '@/views/search/searchlist.vue'
//import prodetail from '@/views/prodetail/index.vue'
//import pay from '@/views/pay/index.vue'
//import myorder from '@/views/myorder/index.vue'

// layout 的二级路由
import home from '@/views/layout/home.vue'
import category from '@/views/layout/category.vue'
import cats from '@/views/layout/cats.vue'
import user from '@/views/layout/user.vue'
import { Toast } from 'vant'

// 组件初始化
Vue.use(VueRouter)

// 创建路由对象
const router = new VueRouter({
  // routes 路由规则们
  routes:[
     {path:'/',component:layout,redirect:'/home',
     children:[
        {path:'/home',component:home},
        {path:'/category',component:category},
        {path:'/cats',component:cats},
        {path:'/user',component:user},
     ]
    
    },
     {path:'/login',component:login},
     {path:'/search',component:search},
     {path:'/searchList',component:searchList},
     {path:'/prodetail',component:() => import('@/views/prodetail')},
     {path:'/pay',component:() => import('@/views/pay')},
     {path:'/myorder',component:() => import('@/views/myorder')},
  ]
})
router.beforeEach((to, from, next) => {
    const token=getInfo().token
    if(to.path==='/pay'||to.path==='/myorder'){
       if(token){
         next()
       }else{
         Toast('尚未登录，请先登录')
         router.push('/login')
       }
    }else{
      next()
    }
 })

// 导出
export default router