import {setInfo,getInfo} from '@/utils/storage.js'
export default {
   namespaced: true,
   state () {
     return {
       userInfo:getInfo()
     }
   },
   // 修改数据的方法
   mutations: {
      setUserInfo(state,obj) {
        // 把数据存到vuex 
         state.userInfo=obj

        //  把数据持久化存储到本地
         setInfo(obj)


      },

      deluserinfo(state){
         state.userInfo={}
      }

   },
   // 处理异步的方法
   actions: {}
 }