import { getCartlist, updatecart, delCart } from '@/api/cart.js'
export default {
  namespaced: true,
  state() {
    return {
      cartlist: []
    }
  },
  // 修改数据的方法
  mutations: {
    setcartlist(state, value) {
      state.cartlist = value
    },
    toggleCheck(state, id) {
      state.cartlist.forEach(item => {
        if (item.goods_id === id) {
          item.isChecked = !item.isChecked
        }
      })
    },

    //全选修改 每一个商品的状态
    toggleAllCheck(state, value) {

      state.cartlist.forEach(item => {
        item.isChecked = value
      })
    }
  },
  // 处理异步的方法
  actions: {
    // 请求数据，把数据给到cartlist
    async getCartAction(context) {
      // 请求后端的数据
      let { data } = await getCartlist()
      // 添加一个是否选中的字段
      data.list.forEach(item => {
        item.isChecked = true
      })
      context.commit('setcartlist', data.list)
    },

    async updatecarts(context, obj) {
      let { value, goods_id, goods_sku_id } = obj
      await updatecart(goods_id, value, goods_sku_id)
      context.dispatch('getCartAction')
    },

    // 删除购物车中的数据
    async delcarts(context) {
      // 要删除的商品的id 数组
      const selgoodslist=context.getters.selgooods
      // 要删除的数组的id 
      const cartids=  selgoodslist.map(item=>item.id)
      // 发请求删除数据
      await delCart(cartids)
      // 获取最新的数据
      context.dispatch('getCartAction')
    }
  },

  //仓库的计算属性
  getters: {
    // 购物车中商品的总数
    goodsTotal(state) {
      let num = 0
      state.cartlist.forEach(item => {
        num += item.goods_num
      })

      return num
    },

    selgooods(state){
     return state.cartlist.filter(item=>item.isChecked)
    },
    // goodsTotal(state){
    //   let num=0
    //    for(let i=0;i<state.cartlist.length;i++){
    //        num+=state.cartlist[i].goods_num
    //    }
    //    return num
    // }

    // 选中商品的总数
    selCount(state) {
      let num = 0
      state.cartlist.forEach(item => {
        if (item.isChecked) {
          num += item.goods_num
        }
      })
      return num
    },

    // 被选中商品的总价

    selPrice(state) {
      let num = 0
      state.cartlist.forEach(item => {
        if (item.isChecked) {
          num += item.goods_num * item.goods.goods_price_min
        }
      })

      return num.toFixed(2)
    },


    // 是否全选
    isAllchecked(state) {
      return state.cartlist.every(item => item.isChecked === true)
    }
  }
}