<template>
  <div class="box">
    <button class="sub" @click="subgoodsnum">-</button>
    <input type="number" :value="goodsNum" />
    <button class="add" @click="addgoodNum">+</button>
  </div>
</template>

<script>
export default {

    props:{
        goodsNum:{
            type:Number,
            default:1
        }
    },

    methods:{
        // 减商品的数据
        subgoodsnum(){

            if(this.goodsNum>1){
                this.$emit('sunNum',this.goodsNum-1)
            }
            
        },

        addgoodNum(){
            this.$emit('addnum',this.goodsNum+1)
        }
    }

};
</script>

<style scoped>
.box {
  width: 122px;
  height: 40px;
  display: flex;
}

.sub{
    width: 30px;
    height: 30px;
    text-align: center;
    line-height: 30px;
    margin-right: 6px;
    border: 0;
}

input{
    width: 50px;
    height: 30px;
    border: 1px solid #ccc;
    border-radius: 3px;
    background-color: #ececec;
    text-align: center;

}

.add{
    width: 30px;
    height: 30px;
    text-align: center;
    line-height: 30px;
    margin-left: 6px;
    border: 0;
}


</style>
