<template>
  <div class="login">
    <van-nav-bar title="会员登录" left-arrow @click-left="$router.go(-1)" />
    <div class="container">
      <div class="title">
        <h3>小米商城</h3>
        <p>未注册的手机号登录后将自动注册</p>
      </div>

      <div class="form">
        <div class="form-item">
          <input
            class="inp"
            maxlength="11"
            placeholder="请输入手机号码"
            type="text"
            @click-left="$router.back()"
            v-model="mobile"
          />
        </div>
        <div class="form-item">
          <input
            class="inp"
            maxlength="5"
            placeholder="请输入图形验证码"
            type="text"
            v-model="picCode"
          />
          <img :src="picSrc" @click="getPic" v-if="picSrc" />
        </div>
        <div class="form-item">
          <input
            class="inp"
            placeholder="请输入短信验证码"
            type="text"
            v-model="numCode"
          />
          <button @click="getCode">
            {{
              totalSecond == second ? "获取验证码" : second + "秒后获取验证码"
            }}
          </button>
        </div>
      </div>

      <div class="login-btn" @click="login">登录</div>
    </div>
  </div>
</template>

<script>
import { getPicCode, getNumCode, userLogin } from "@/api/login.js";
import { Toast } from 'vant';
import { mapMutations } from "vuex";
export default {
  name: "LoginPage",
  data() {
    return {
      picKey: "", //验证码图片的唯一标识
      picCode: "", //用户输入的图形验证码
      picSrc: "", //后端返回的图片地址

      totalSecond: 5, // 总的一个时间
      second: 5, //当前是多少秒

      timerId: null, //定时器的id

      mobile: 18917286702, //用户的手机号

      numCode: null, //短信验证码
    };
  },
  methods: {
    ...mapMutations('user',['setUserInfo']),
    async getPic() {
      const picdata = await getPicCode();
      console.log(picdata);
      this.picKey = picdata.data.key;
      this.picSrc = picdata.data.base64;
    },

    // 用于手机号和验证码的校验
    validFn() {
      // 手机号校验
      if (!/^1[3-9]\d{9}$/.test(this.mobile)) {
        this.$toast("请输入正确的手机号");
        return false;
      }
      //  验证码校验
      if (!/^[A-Za-z0-9]{4}$/.test(this.picCode)) {
        this.$toast("请输入正确的验证码");
        return false;
      }
      return true;
    },

    // 获取验证码
    async getCode() {
      // 点击一下就开启了一个定时器，（如果当前有定时器开着，不再开了）
      // second 一直自减少，减到最后是负数（<1 关闭定时器，重新）

      // 判断当前是否有定时器开着，如果没有定时器开着 我们在开定时器
      // 并且倒计时结束
      if (!this.timerId && this.second == this.totalSecond) {
        // 发送请求

        if (!this.validFn()) {
          return;
        }

        await getNumCode(this.picCode, this.picKey, this.mobile);
        this.timerId = setInterval(() => {
          this.second--;

          if (this.second < 1) {
            // 关闭定时器
            clearInterval(this.timerId);
            // 重置倒计时
            this.second = this.totalSecond;
            // 清除timerid
            this.timerId = null;
          }
        }, 1000);
      }
    },
 

    // 登录相关
   async login(){
    // 判断手机号和验证码输入是否正确
    //  if(!this.validFn()){
    //   return
    //  }

    //  if(!/^\d{6}$/.test(this.numCode)){
    //   Toast("验证码为6为的数字，请检查输入是否正确")
    //   return
    //  }
    const obj= await userLogin(this.numCode,this.mobile)
    console.log(obj);
    this.setUserInfo(obj.data)

    const mypath=this.$route.query.allpath||'/'
     this.$router.push({
      path:mypath
     })
     Toast("欢迎回来")
    }
    
  },

  created() {
    // this.$toast.loading({
    //   message: "加载中...",
    //   forbidClick: true,
    // });
    this.getPic();
  },

  destroyed() {
    clearInterval(this.timerId);
  },
};
</script>

<style lang="less" scoped>
.container {
  padding: 49px 29px;

  .title {
    margin-bottom: 20px;
    h3 {
      font-size: 26px;
      font-weight: normal;
    }
    p {
      line-height: 40px;
      font-size: 14px;
      color: #b8b8b8;
    }
  }

  .form-item {
    border-bottom: 1px solid #f3f1f2;
    padding: 8px;
    margin-bottom: 14px;
    display: flex;
    align-items: center;
    .inp {
      display: block;
      border: none;
      outline: none;
      height: 32px;
      font-size: 14px;
      flex: 1;
    }
    img {
      width: 94px;
      height: 31px;
    }
    button {
      height: 31px;
      border: none;
      font-size: 13px;
      color: #cea26a;
      background-color: transparent;
      padding-right: 9px;
    }
  }

  .login-btn {
    width: 100%;
    height: 42px;
    margin-top: 39px;
    background: linear-gradient(90deg, #ecb53c, #ff9211);
    color: #fff;
    border-radius: 39px;
    box-shadow: 0 10px 20px 0 rgba(0, 0, 0, 0.1);
    letter-spacing: 2px;
    display: flex;
    justify-content: center;
    align-items: center;
  }
}
</style>
